import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state:{
       cartList:[]
	},
	mutations:{
       addcart(state,product){
        //查找之前是否有该商品
         let oldProduct = state.cartList.find(item => item.iid === product.iid)
        //判断oldProduct
        if(oldProduct){
           oldProduct.count +=1;
        }else{
           product.count = 1;
           state.cartList.push(product)
        }
       }
	}
})

export default store;